import React, { Component } from 'react';
import logo from '../global/logo.png';

class Header extends Component {
  render(){
      return(
      <div className="Header">
        <div className="page-header" id="banner">
            <div className="row">
              <div className="col-xs-6 col-lg-3">
                <img src={logo} className="logo_central"/>
              </div>
              <div className="col-xs-6 col-lg-9">
                    <form className="form-inline float-right">
                    <div className="form-group">
                      <input type="text" className="form-control form-control-sm inputform" id="exampleInputName2" placeholder="Jane Doe"/>
                    </div>
                    <div className="form-group">
                      <input type="email" className="form-control form-control-sm inputform1" id="exampleInputEmail2" placeholder="jane.doe@example.com"/>
                    </div>
                    <button type="submit" className="btn btn-warning btn-sm ">Ingreso Capitanes</button>
                  </form>
              </div>
            </div>
        </div>
      </div>
      );
  }
}

export default Header;



