import React, { Component } from 'react';
import { Card, CardImage, Button, CardTitle, CardText, CardBody, Fa } from 'mdbreact';
import banner_  from '../global/home/banner_.jpg';


import carruse1 from '../global/carrusel/carrusel1.png';
import carruse2 from '../global/carrusel//carrusel2.png';
import carruse3 from '../global/carrusel//senior.jpg';
import carruse4 from '../global/carrusel//carrusel4.png';

import femenino from '../global/home/card/femenino.png';
import senior from '../global/home/card/senior.jpg';
import relampago from '../global/home/card/card3.jpg';

class App extends Component {
  render() {
    return (
      <div className="App">
        <div className="Home">
          <div className="row">
              <div className="col-xs-6 col-lg-8 mh-20">
                <div id="carouselExampleIndicators" className="carousel slide" data-ride="carousel">
                  <ol className="carousel-indicators">
                    <li data-target="#carouselExampleIndicators" data-slide-to="0" className="active"></li>
                    <li data-target="#carouselExampleIndicators" data-slide-to="1"></li>
                    <li data-target="#carouselExampleIndicators" data-slide-to="2"></li>
                    <li data-target="#carouselExampleIndicators" data-slide-to="3"></li>
                  </ol>
                  <div className="carousel-inner">
                    <div className="carousel-item active">
                      <img className="d-block w-100 carrusel_imagen_ultimo" src={carruse1} alt="First slide" />
                    </div>
                    <div className="carousel-item">
                      <img className="d-block w-100 carrusel_imagen_ultimo" src={carruse2} alt="Second slide"/>
                    </div>
                    <div className="carousel-item">
                      <img className="d-block w-100 carrusel_imagen_ultimo" src={carruse3} alt="Third slide"/>
                    </div>
                    <div className="carousel-item">
                      <img className="d-block w-100 carrusel_imagen_ultimo" src={carruse4} alt="Third slide"/>
                    </div>
                  </div>
                  <a className="carousel-control-prev" href="#carouselExampleIndicators" role="button" data-slide="prev">
                    <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                    <span className="sr-only">Previous</span>
                  </a>
                  <a className="carousel-control-next" href="#carouselExampleIndicators" role="button" data-slide="next">
                    <span className="carousel-control-next-icon" aria-hidden="true"></span>
                    <span className="sr-only">Next</span>
                  </a>
                </div>
            </div>
            <div className="col-xs-6 col-lg-4">
                <div className="menu_lateral">
                      <ul class="list-inline">
                        <li class="list-inline-item text-uppercase mb-5 font-weight-bold titulos_blancos centrar_text aezz">Ultimos Resultados</li>
                      </ul>
                </div>
            </div>
            <div className="col-xs-12 col-lg-12 banner_central">
              <img src={banner_} className=""/>
            </div>
            <p></p>
            <p></p>
            <div className="col-xs-4 col-lg-4">
              <Card>
                <CardImage top src={senior} overlay="white-slight" hover waves alt="Card image cap"/>
                <CardBody className="elegant-color white-text rounded-bottom">
                  <a className="activator waves-effect waves-light mr-4"><Fa icon="share-alt"></Fa></a>
                  <CardTitle>Liga Senior</CardTitle>
                  <hr className="hr-light" />

                  <a href="#!" className="black-text d-flex justify-content-end"><h5 className="white-text">Ir <Fa icon="angle-double-right"></Fa></h5></a>
                </CardBody>
              </Card>
            </div>

           <div className="col-xs-4 col-lg-4">
             <Card>
              <CardImage top src={femenino} overlay="white-slight" hover waves alt="Card image cap" className="card_imagen"/>
              <CardBody className="elegant-color white-text rounded-bottom">
                <a className="activator waves-effect waves-light mr-4"><Fa icon="share-alt"></Fa></a>
                <CardTitle>Liga Femenina</CardTitle>
                <hr className="hr-light" />

                <a href="#!" className="black-text d-flex justify-content-end"><h5 className="white-text">Ir <Fa icon="angle-double-right"></Fa></h5></a>
              </CardBody>
            </Card>
           </div>

           <div className="col-xs-4 col-lg-4">
             <Card>
              <CardImage top src={relampago} overlay="white-slight" hover waves alt="Card image cap" className="card_imagen"/>
              <CardBody className="elegant-color white-text rounded-bottom">
                <a className="activator waves-effect waves-light mr-4"><Fa icon="share-alt"></Fa></a>
                <CardTitle>Liga Relampago</CardTitle>
                <hr className="hr-light" />

                <a href="#!" className="black-text d-flex justify-content-end"><h5 className="white-text">Ir <Fa icon="angle-double-right"></Fa></h5></a>
              </CardBody>
            </Card>
           </div>
          </div>
        </div>
      </div>
    );
  }
}

export default App;



