import React from "react";
import { BrowserRouter , Route, Switch } from 'react-router-dom';


import Home from '../component/Home';
import Liga from '../component/Liga';
import Resultados from '../component/Resultados';
import Informacion from '../component/Informacion';
import Contacto from '../component/Contacto';

export default () => (
  <BrowserRouter>
    <Switch>
        <Route exact path="/" component={Home} />
        <Route exact path="/liga" component={Liga} />
        <Route exact path="/resultados" component={Resultados} />
        <Route exact path="/informacion" component={Informacion} />
        <Route exact path="/contacto" component={Contacto} />
    </Switch>
  </BrowserRouter>
);



