import React, { Component } from 'react';
import { Col, Container, Row, Footer, Badge, Fa} from 'mdbreact';

class Liga extends Component {
  render() {
    return (
      <div className="App">
        <Footer color="unique-color-dark" className="page-footer font-small pt-0">
            <div style={{backgroundColor: 'rgb(48, 77, 143)'}}>
                <Container>
                    <Row className="py-4 d-flex align-items-center">
                        <Col md="6" lg="5" className="text-center text-md-left mb-4 mb-md-0">
                            <h6 className="text-uppercase mb-0 white-text font-weight-bold">Ultimos Resultados</h6>
                        </Col>
                        <Col md="6" lg="7" className="text-center text-md-right">
                            <li className="list-inline-item">  <a className="fb-ic ml-5"><Badge color="default"><Fa icon="facebook" aria-hidden="true" size="2x"/></Badge></a></li>
                            <li className="list-inline-item"><a className="tw-ic"><Badge color="primary"><Fa icon="instagram" aria-hidden="true" size="2x"/></Badge></a></li>
                            <li className="list-inline-item"><a className="gplus-ic"><Badge color="success"><Fa icon="whatsapp" aria-hidden="true" size="2x"/></Badge></a></li>

                        </Col>
                    </Row>
                </Container>
            </div>
            <Container className="mt-5 mb-4 text-center text-md-left">
                <Row className="mt-12">
                    <Col md="6" lg="6" xl="6" className="mb-4">
                        <h6 className="text-uppercase font-weight-bold"><strong>Proximamente</strong></h6>
                        <hr className="deep-purple accent-2 mb-4 mt-0 d-inline-block mx-auto" style={{width: '60px'}}/>

                    </Col>
                </Row>
            </Container>

        </Footer>
      </div>
    );
  }
}

export default Liga;



